


































import Layout from "@/components/Layout.vue";
import Types from "@/components/Count_Types.vue";
import Tabs from "@/components/Statistics_Tabs.vue";
import { Component, Watch } from "vue-property-decorator";
import intervalList from "@/constants/intervalList";
import dayjs from 'dayjs';
import deepClone from '@/lib/deepClone'
import Icon from "@/components/icon.vue";
import { mixins } from "vue-class-component";
import { JudgeDate }from "@/mixins/JudgeDate";
import _ from 'lodash'

/* import ECharts from "vue-echarts"
import 'echarts/lib/chart/bar'
import "echarts" */

import Chart from "@/components/Chart.vue"

@Component({
  components: { Layout, Types, Tabs, Icon, Chart },
})

export default class Statistics extends mixins(JudgeDate) {
  
  type = "-";
  interval = "day";
  intervalList = intervalList;
  
  mounted(){
    const div = (this.$refs.chartWrapper as HTMLDivElement)
    div.scrollLeft = div.scrollWidth;
  }

  toTagString(tags: Tag[]){
    return tags.length === 0 ? '无' :tags.join(',')
  }
  beforeCreate(){
    this.$store.commit('fetchRecords')
  }
  get recordList(){
    return (this.$store.state as RootState).recordList;
  }
  get timeGroupList(){
    const {recordList} = this;
  
    const newList = deepClone(recordList).filter(record => record.type === this.type)
      .sort((a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
  
    if(newList.length===0){return [];}

    type Result = {
      title: string,
      total?: number, 
      items: RecordItem[]
    }[]
    //相同日期分成同一组 上面sort排序过了 第一个一定是最近的
    const result:Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items:[newList[0]]}];

    for(let i=1; i<newList.length; i++){
      const current = newList[i];
      const last = result[result.length - 1];
      if(dayjs(last.title).isSame(dayjs(current.createdAt), 'day')){
        last.items.push(current);
      }else{
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group =>{
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    })
    return result;
  }

  get dateTotalPair(){
    const today = new Date();
    const array = [];
    for(let i=0; i<=29; i++){
      //获取近30天日期
      const date = dayjs(today).subtract(i, 'day').format('YYYY-MM-DD');
      //找到当天的金额
      const found = _.find(this.timeGroupList, {title: date});
      array.push({
        date: date, 
        value: found ? found.total : 0,  //有则统计无则0
        });
    }
    return array.reverse();
  }

  get lineChartOption(){
    const array = this.dateTotalPair.map(item => item.date);
    for(let i=0; i<array.length; i++){
      array[i] = this.judgeDate(array[i])
    }
    const keys = array;

    const values = this.dateTotalPair.map(item => item.value);
    return{
      title:{
        show: true,
        text: '近30天统计',
        right: '50%',
      },
      grid:{
        left: 16,
        right: 16,
      }, 
      dataZoom: {
        start: 80,
        type: "inside"
      },
      xAxis: {
        type: 'category',
        data: keys,
        axisTick: {
          alignWithLabel: true
        },
        axisLabel:{
          interval:0,
          rotate:40,
        },
      },
      yAxis: {
        type: 'value',
        show: false,
      },
      series: [{
        symbolSize: 10,
        data: values,
        type: 'line',
        itemStyle: {
          normal: {
            label: {
              show: true, //开启显示
              position: 'top', //在上方显示
              textStyle: { //数值样式
              color: '#999',
              fontSize: 14,
              }
            }
          },
        },
      }],
     
    }
  }

}
