

































import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component
export default class Count_NumberPad extends Vue {
  @Prop(Number) readonly value!: number;
  output = this.value.toString();

  counting = 0;
  countingType: string | undefined;
  countingStatus = false; 

  inputContent(event: MouseEvent): void {
    const button = event.target as HTMLButtonElement;
    const input = button.textContent!;
    if (this.output.length === 12) {
      return;
    }
    if (this.output.indexOf(".") >= 0) {
      const index = this.output.indexOf(".");
      if (this.output[index + 2]) {
        if("0123456789".indexOf(input) >= 0 && this.countingStatus === false){
          return; //最多两位小数！
        }
      }
    }

    if (this.output === "0") {
      if (input === "0") {
        return;
      } //防止出现'00'的情况
      if ("0123456789".indexOf(input) >= 0) {
        this.output = input;
        //第一个数字为0时，输入其他数字会替换0
      } else {
        this.output += input;
      }
      return;
    }
    if (this.output.indexOf(".") >= 0 && input === ".") {
      return;
    } //防止出现'..'的情况
    if(this.countingStatus === true){
      this.output = input;
      this.countingStatus = false;
      return;
    }
    this.output += input;
  }
  remove() {
    if (this.output.length !== 1) {
      this.output = this.output.substring(0, this.output.length - 1);
    } else {
      this.output = "0";
    }
  }
  clear() {
    this.output = "0";
    this.countingType = undefined;
    this.counting = 0;
  }
  ok() {
    this.equal();
    const number = parseFloat(this.output);
    this.$emit("update:value", number);
    this.$emit("submit", number);
    this.output = "0";
  }
  add() {
    this.counting = (this.counting*100 + parseFloat(this.output)*100) /100;
    this.countingType = "+";
    this.output = this.counting.toString();
    this.countingStatus = true;
  }
  subtract() {
    this.counting = (this.counting*100 + parseFloat(this.output)*100) /100;
    this.countingType = "-";
    this.output = this.counting.toString();
    this.countingStatus = true;
  }
  equal() {
    if (this.countingType === "+") {
      this.counting = (this.counting*100 + parseFloat(this.output)*100) /100;
    } else if (this.countingType === "-") {
      this.counting = (this.counting*100 - parseFloat(this.output)*100) /100;
    } else {
      return;
    }
    this.output = this.counting.toString();
    //浮点数运算结果不精确问题
    if (this.output.indexOf(".") >= 0) {
      const index = this.output.indexOf(".");
      let temp = '';
      if (this.output[index + 2] !== '0' &&  this.output[index + 2] !== undefined) {
        for(let i=0;i<=index+2; i++){
          temp += this.output[i]
        }
        this.output = temp;
      }else if(this.output[index + 1] !== '0' &&  this.output[index + 1] !== undefined){
        for(let i=0;i<=index+1; i++){
          temp += this.output[i]
        }
        this.output = temp;
      }
    }
      this.countingType = undefined;
      this.counting = 0;
    
  }
  
}
